import { Link } from 'react-router-dom';
import { Button } from 'reactstrap';

function Cover() {
    return (
        <div className="coverDiv">
          <div className="overlay"></div>
          {/* <video playsinline="playsinline" autoplay="autoplay" muted="muted" loop="loop">
            <source src="assets/videos/milkyway.mp4" type="video/mp4" />
          </video> */}
          <img  className="coverImg" src="assets/images/planet.png" alt="planet" />
          <div className="container h-75">
            <div className="d-flex h-100  align-items-center">
              <div className="w-100 text-white">
                <h1 className="display-3"><strong>Marketing.</strong></h1>
                <h1 className="display-3"><strong>Analytics.</strong></h1>
                <h1 className="display-3"><strong>Results.</strong></h1>
              </div>
            </div>
            <br></br>
            <div className="d-flex justify-content-center">
            <Link to="/our-services">
              <Button color="info" className=" btnlightBlue" role="button"><img width="30" height="30" src="assets/images/services-icon.svg" alt="services" /> <strong>See our services</strong></Button >
              </Link>
            </div>
            <div className="d-flex w-100 justify-content-center">
              <Link to="/contact-us" className="link" ><strong>or <u> Get in Touch</u></strong></Link>
            </div>
          </div>
        </div>
    );
  }
  
  export default Cover;