import { Card, CardBody, Jumbotron } from 'reactstrap';
import Fade from 'react-reveal/Fade';
import Footer from './FooterComponent';


function RenderCard(props) {
  return (
    <Card>
      <CardBody>
        <div className="row justify-content-center">
          <img className="py-4" src={props.image} alt="Cortech" />
        </div>
        <div className="row ">
          <h4>{props.title}</h4>
          <p>{props.description}</p>
        </div>
      </CardBody>
    </Card>);
}


function Services() {
  return (
    <div>
      <Jumbotron>
        <div className="container">
          <div className="row row-header">
            <div className="col-12 col-sm-6">
              <h1>Our services</h1>
            </div>
          </div>
        </div>
      </Jumbotron>


      <div className="container">

        <Fade left cascade>
          <div className="row">
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/seo.png" title="SEO" description="Our skilled team will assist you to improve your website in order to get great positioning in all search engines. We are always up to date with the latest changes and trends." />
            </div>
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/affiliate.png" title="Affiliate marketing" description="When it comes to business marketing you need to have reach. We work very hard at creating and molding relationships that are extremely beneficial for all parties involved to reach similar goals and make everyone on the same side profitable. We have specialized front and back end software available to track your marketing stats as everything smoothly trends upward!" />
            </div>
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/reporting.png" title="Reporting" description="At Cortech we provide a package of tools and services that will help you create, improve and administrate reports for your company. Our user friendly tools are fully customizable and can be adapted to your demands." />
            </div>
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/ppc.png" title="PPC (Pay per Click)" description="We can help you reach your target audience within budget. Since clicks are a way to measure attention and interest, you can use our Reporting tools to easily measure the results." />
            </div>
          </div>
        </Fade >

        <hr></hr>

        <Fade right>
          <div className="row">
            <div className="col-md-10 offset-md-1">
              <blockquote className="blockquote">
                <p className="mb-0 quote">"I think it’s very important to have a feedback loop, where you’re constantly thinking about what you’ve done and how you could be doing it better."</p>
                <footer className="blockquote-footer">Elon Musk</footer>
              </blockquote>
            </div>
          </div>
        </Fade>

        <hr></hr>

        <Fade left cascade>
          <div className="row">
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/email.png" title="Email marketing" description="Email Marketing Our anti spam and whitelist strategies guarantee email delivery of your newsletters and any other content you require to distribute to your customer base. Customers receiving your marketing content is half the battle!" />
            </div>
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/network.png" title="Network and infrastructure" description="We provide the infrastructure and servers, to achieve your technological needs. Our package of solutions also include cloud computing, physical and virtual servers to ensure you have the latest technology at your disposition." />
            </div>
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/crm.png" title="Customer relationship management" description="Having your customer base handy and available to your staff is not an easy task. At Cortech Marketing we bring processes, technology and your customer base together for a full client satisfaction. Our CRM solutions are fully customizable and will help you drive success by making all your staff part of the relationship building process." />
            </div>
            <div className="col-12 col-sm-6 col-md-3">
              <RenderCard image="assets/images/conversion.png" title="Conversion" description="You are only as good as your results. And you can only get results by finding problem areas, fixing them and increasing your conversion rates as consistently as possible." />
            </div>
          </div>
        </Fade >
        <hr></hr>

      </div>
      <Footer />
    </div>
  );
}

export default Services;