import React, { Component } from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isNavOpen: false
    }
    this.toggleNav = this.toggleNav.bind(this);
  }

  toggleNav() {
    this.setState({
      isNavOpen: !this.state.isNavOpen
    });
  }

  


  render() {

    const checkActive = (match, location) => {
      //some additional logic to verify you are in the home URI
      if(!location) return false;
      const {pathname} = location;
      console.log(pathname);
      return pathname === "/";
  }
    return (
      <React.Fragment>
        <Navbar dark expand="md" className=" nav-masthead ">
          <div className="container">
            <NavbarToggler onClick={this.toggleNav} />
            <NavbarBrand className="mr-auto" href="/">
              <img src="assets/images/logo.png" height="30" width="133" alt="Cortech" />
            </NavbarBrand>
            <Collapse isOpen={this.state.isNavOpen} navbar>
              <Nav navbar>
                <NavItem>
                  <NavLink className="nav-link ml-md-3" to="/" isActive={checkActive}>About Us</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className="nav-link" to="/our-services"> Services</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className="nav-link" to="/contact-us"> Contact</NavLink>
                </NavItem>
              </Nav>
              <Nav className="ml-auto" navbar>
                <NavItem>
                </NavItem>
              </Nav>
            </Collapse>
          </div>
        </Navbar>
        
        



      </React.Fragment>
    );
  }
}

export default Header;