import { Jumbotron } from 'reactstrap';
import { Link } from 'react-router-dom';
import Footer from './FooterComponent';
import Zoom from 'react-reveal/Zoom';

function Contact() {
  return (
    <div>
      <Jumbotron>
        <div className="container">
          <div className="row row-header">
            <div className="col-12 col-sm-6">
              <h1>Contact Us </h1>
            </div>
          </div>
        </div>
      </Jumbotron>

      <Zoom cascade>
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h1><strong>Contact us for any of <Link to="/our-services"><u>our services</u></Link> and more. We are always happy to offer advice and discuss a package that covers your needs.</strong></h1>
            </div>
          </div>

          <hr></hr>
          <div className="row">
            <div className="col-12">
              <h5><strong>We look forward to hearing from you!</strong></h5>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <a className="btn btn-primary roundedBtn" href="mailto:confusion@food.net" role="button"><img width="30" height="30" src="assets/images/envelope-icon.svg" alt="email" /> <strong>Get in touch</strong></a>
            </div>
          </div>
          <hr></hr>
          <div className="row">
            <div className="col-12">
              <h5><strong>Follow us on social media!</strong></h5>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <a className="btn btn-social-icon btn-secondary roundedBtn" href="#"><i className="fa fa-facebook"></i></a> <p>&nbsp; Coming soon...</p>
            </div>
          </div>
          <hr></hr>
          <div className="row">
            <div className="col-12">
              <h5><strong>Call us</strong></h5>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <a className="underline" href="tel:+506 6137 3460"><span className="fa fa-phone" />  +xxx xxxx xxxx</a>
            </div>
          </div>
        </div>
      </Zoom>
      <br></br>
      <Footer />
    </div>

  );
}

export default Contact;