import Fade from 'react-reveal/Fade';

function Footer() {
  return (
    <footer className="footer">
      <Fade bottom >
        <div className="footerDiv">
          <div className="overlay"></div>
          {/* <video playsinline="playsinline" autoplay="autoplay" muted="muted" loop="loop">
        <source src="assets/videos/milkyway.mp4" type="video/mp4" />
      </video> */}
          <img className="coverImg" src="assets/images/footer.jpg" alt="footer" />
          <div className="container h-75">
            <div className="d-flex h-100  align-items-center justify-content-center">
              <a className="btn btn-primary roundedBtn d-none d-md-block" href="mailto:confusion@food.net" role="button">
                <div className="container">
                  <div className="row ">
                    <div className="col-4">
                      <img src="assets/images/envelope-icon.svg" width="70" height="100" alt="email" />
                    </div>
                    <div className="col-6">
                      <div className="row ">
                        <h1><strong>Get in touch</strong></h1>
                        <p>We look forward to hearing from you!</p>
                      </div>
                    </div>
                  </div>

                </div>
              </a>
              <div className="row d-md-none">
                <a className="btn btn-primary roundedBtn" href="mailto:confusion@food.net" role="button"><img width="30" height="30" src="assets/images/envelope-icon.svg" alt="email" /> <strong>Get in touch</strong></a>
              </div>
              
            </div>
          </div>
        </div>
        <div className="text-center p-3 copyRight">
          <p >© Copyright 2021 - CorTechMarketing. All Rights Reserved.</p>
          <p> Cortech LTD, 40 Linewood Street, P.O. Box 2503, Grand Cayman Islands. </p>
        </div>
      </Fade>
    </footer>
  );
}

export default Footer;