import Cover from './CoverComponent'
import Footer from './FooterComponent';
import { Card, CardBody } from 'reactstrap';
import Fade from 'react-reveal/Fade';

function Home() {
  return (
    <div>
      <Cover />
      <div className="container">
        <br></br>
        <Fade left>
          <div className="row">
            <div className="col-md-10 offset-md-1">
              <blockquote className="blockquote">
                <p className="mb-0 quote">"Logic will get you from A to B, imagination will take you everywhere."</p>
                <footer className="blockquote-footer">Albert Einstein</footer>
              </blockquote>
            </div>
          </div>
        </Fade>

        <br></br>
        <Fade right>
          <Card id="features">
            <CardBody>
              <div className="row">
                <div className="col-md-4 d-flex justify-content-center ">
                  <img className="py-4" src="assets/images/why.png" alt="Cortech" />
                </div>
                <div className="col-md-8 order-md-2">
                  <h2>Why choose CorTech?</h2>
                  <p>Would you like better rankings in search engines and improve your revenue? Do you require a CRM tool that accommodates to your business? Would you like an integrated software platform to measure your results? Cortech offers a variety of packages with dynamic tools that can be easily adapted to your needs. We work with all kinds of companies and we guarantee fast and easy results.</p>
                </div>

              </div>
            </CardBody>
          </Card>
        </Fade>
        <br></br>
        <Fade left>
          <Card>
            <CardBody>
              <div className="row">
                <div className="col-md-4 d-flex justify-content-center  order-md-2">
                  <img className="py-4" src="assets/images/our-team.png" alt="Cortech" />
                </div>
                <div className="col-md-8  order-md-1">
                  <h2>Our team</h2>
                  <p>Cortech specializes in Internet Marketing and IT solutions. We have personnel specialized in marketing, SEO, graphic design, web design, IT, software development and sales, programming and more! With a variety of creative members dedicated and passionate and goal oriented. You are in good hands with this group of accomplished professionals.</p>
                </div>


              </div>
            </CardBody>
          </Card>
        </Fade>
        <br></br>
        <Fade right>
          <Card>
            <CardBody>
              <div className="row">
                <div className="col-md-4 d-flex justify-content-center">
                  <img className="py-4" src="assets/images/action.png" alt="Cortech" />
                </div>
                <div className="col-md-8">
                  <h2>Action</h2>
                  <p>We take pride on ensuring that we attend every industry related conference, online presentation and keep up with the content to stay ahead of the latest trends in this ever changing industry.</p>
                </div>
              </div>
            </CardBody>
          </Card>
        </Fade>

        <br></br>
      </div>
      <Footer />
    </div>
  );
}

export default Home;